.calmdown-default-theme {
  display: flex;
  border: 1px solid #d1d1d1;
  flex-direction: column;
  overflow: hidden; }
  .calmdown-default-theme .cd-toolbar {
    padding: 5px;
    background-color: #f8f8f8;
    border-bottom: 1px solid #d1d1d1; }
  .calmdown-default-theme .cd-body {
    flex: 1;
    display: flex; }
    .calmdown-default-theme .cd-body .cd-editor {
      flex: 1 1 50%;
      max-width: 50%;
      padding: 10px;
      border: 0;
      background-color: white;
      border-right: 1px solid #d1d1d1;
      resize: none;
      overflow-y: auto;
      overflow-x: hidden; }
      .calmdown-default-theme .cd-body .cd-editor:focus {
        outline: 0; }
    .calmdown-default-theme .cd-body .cd-preview {
      flex: 1 1 50%;
      max-width: 50%;
      padding: 10px;
      background-color: white;
      word-wrap: break-word;
      overflow-y: auto;
      overflow-x: hidden; }
  .calmdown-default-theme .cd-resize {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10.5px;
    background-color: #f8f8f8;
    border-top: 1px solid #d1d1d1;
    cursor: s-resize; }
    .calmdown-default-theme .cd-resize .cd-bars {
      width: 25px;
      height: 1px;
      position: relative;
      background-color: #d1d1d1; }
      .calmdown-default-theme .cd-resize .cd-bars:before, .calmdown-default-theme .cd-resize .cd-bars:after {
        content: '';
        display: block;
        position: absolute;
        width: 25px;
        height: 1px;
        background-color: #d1d1d1; }
      .calmdown-default-theme .cd-resize .cd-bars:before {
        top: -2px; }
      .calmdown-default-theme .cd-resize .cd-bars:after {
        bottom: -2px; }

.cd-highlight-default {
  /**
   * okaidia theme for JavaScript, CSS and HTML
   * Loosely based on Monokai textmate theme by http://www.monokai.nl/
   * @author ocodia
   */
  /* Code blocks */
  /* Inline code */ }
  .cd-highlight-default code[class*="language-"],
  .cd-highlight-default pre[class*="language-"] {
    color: #f8f8f2;
    background: none;
    text-shadow: 0 1px rgba(0, 0, 0, 0.3);
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    tab-size: 4;
    hyphens: none; }
  .cd-highlight-default pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
    border-radius: 0.3em; }
  .cd-highlight-default :not(pre) > code[class*="language-"],
  .cd-highlight-default pre[class*="language-"] {
    background: #272822; }
  .cd-highlight-default :not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
    white-space: normal; }
  .cd-highlight-default .token.comment,
  .cd-highlight-default .token.prolog,
  .cd-highlight-default .token.doctype,
  .cd-highlight-default .token.cdata {
    color: slategray; }
  .cd-highlight-default .token.punctuation {
    color: #f8f8f2; }
  .cd-highlight-default .namespace {
    opacity: .7; }
  .cd-highlight-default .token.property,
  .cd-highlight-default .token.tag,
  .cd-highlight-default .token.constant,
  .cd-highlight-default .token.symbol,
  .cd-highlight-default .token.deleted {
    color: #f92672; }
  .cd-highlight-default .token.boolean,
  .cd-highlight-default .token.number {
    color: #ae81ff; }
  .cd-highlight-default .token.selector,
  .cd-highlight-default .token.attr-name,
  .cd-highlight-default .token.string,
  .cd-highlight-default .token.char,
  .cd-highlight-default .token.builtin,
  .cd-highlight-default .token.inserted {
    color: #a6e22e; }
  .cd-highlight-default .token.operator,
  .cd-highlight-default .token.entity,
  .cd-highlight-default .token.url,
  .cd-highlight-default .language-css .token.string,
  .cd-highlight-default .style .token.string,
  .cd-highlight-default .token.variable {
    color: #f8f8f2; }
  .cd-highlight-default .token.atrule,
  .cd-highlight-default .token.attr-value,
  .cd-highlight-default .token.function {
    color: #e6db74; }
  .cd-highlight-default .token.keyword {
    color: #66d9ef; }
  .cd-highlight-default .token.regex,
  .cd-highlight-default .token.important {
    color: #fd971f; }
  .cd-highlight-default .token.important,
  .cd-highlight-default .token.bold {
    font-weight: bold; }
  .cd-highlight-default .token.italic {
    font-style: italic; }
  .cd-highlight-default .token.entity {
    cursor: help; }
